import { useState } from "react";
import {  Menu, MenuItem } from "react-pro-sidebar";
import { Box, Button, IconButton, Typography, useTheme } from "@mui/material";
import { Link , useNavigate} from "react-router-dom";
import 'react-pro-sidebar/dist/css/styles.css';
import { tokens } from "../../../theme";
import HomeOutlinedIcon from "@mui/icons-material/HomeOutlined";
import ReceiptOutlinedIcon from "@mui/icons-material/ReceiptOutlined";
import MenuOutlinedIcon from "@mui/icons-material/MenuOutlined";
import LogoutIcon from "@mui/icons-material/ExitToApp";
import { ProSidebar } from 'react-pro-sidebar';
import PeopleOutlined from "@mui/icons-material/PeopleOutlined";

const Item = ({ title, to, icon, selected, setSelected }) => {
  const theme = useTheme();
  const colors = tokens(theme.palette.mode);
  return (
    <MenuItem
      active={selected === title}
      style={{
        color: colors.grey[100]
      }}
      onClick={() => setSelected(title)}
      icon={icon}
    >
      <Typography>{title}</Typography>
      <Link to={to} />
    </MenuItem>
  );
};

const Sidebar = () => {
  const theme = useTheme();
  const colors = tokens(theme.palette.mode);
  const [isCollapsed, setIsCollapsed] = useState(false);
  const [selected, setSelected] = useState("Dashboard");
  const navigate=useNavigate()

  const handleLogout = ()=>{
    sessionStorage.removeItem('Token')
    navigate('/login')
  }

  const bull = (
    <Box
      component="span"
      sx={{ display: 'inline-block', mx: '2px', transform: 'scale(0.8)' }}>•</Box>
  )

  return (
    <Box
      sx={{
        "& .pro-sidebar-inner": {
          background: `${colors.primary[400]} !important`,
        },
        "& .pro-icon-wrapper": {
          backgroundColor: "transparent !important",
        },
        "& .pro-inner-item": {
          padding: "5px 35px 15px 20px !important",
        },
        "& .pro-inner-item:hover": {
          color: "#868dfb !important",
        },
        "& .pro-menu-item.active": {
          color: "#6870fa !important",
        },
      }}
    >
      <ProSidebar collapsed={isCollapsed} style={{width: "300px"}}>
        <Menu iconShape="square">
          {/* LOGO AND MENU ICON */}
          <MenuItem
            onClick={() => setIsCollapsed(!isCollapsed)}
            icon={isCollapsed ? <MenuOutlinedIcon /> : undefined}
            style={{
              margin: "5px 0 20px 0",
              color: colors.grey[100],
            }}
          >
            {!isCollapsed && (
              <Box
                display="flex"
                justifyContent="space-between"
                alignItems="center"
                ml="15px"
              >
                <Typography variant="h3" color={colors.grey[100]}>
                  ADMIN
                </Typography>
                <IconButton onClick={() => setIsCollapsed(!isCollapsed)}>
                  <MenuOutlinedIcon />
                </IconButton>
              </Box>
            )}
          </MenuItem>

          {!isCollapsed && (
            <Box mb="25px">
              <Box display="flex" justifyContent="center" alignItems="center">
                <img
                  alt="profile"
                  width="100px"
                  height="100px"
                  src={'data:image/jpeg;base64,'}
                  style={{ cursor: "pointer", borderRadius: "50%" }}
                />
              </Box>
              <Box textAlign="center">
                <Typography
                  variant="h2"
                  color={colors.grey[100]}
                  fontWeight="bold"
                  sx={{ m: "10px 0 0 0" }}
                >
                  Aswathy Geetha
                </Typography>
                <Typography variant="h5" color={colors.greenAccent[500]}>
                  ICT Academy of Kerala
                </Typography>
              </Box>
            </Box>
          )}

          <Box paddingLeft={isCollapsed ? undefined : "10%"}>
            <Item
              title="DASHBOARD"
              to="/dashadmin"
              icon={<HomeOutlinedIcon />}
              selected={selected}
              setSelected={setSelected}
            >/</Item><br/>
           <Item
              title="PROJECT"
              to="/dashadmin"
              icon={<ReceiptOutlinedIcon />}
              selected={selected}
              setSelected={setSelected}
            >/</Item>
            <Item
              title="Project Topics"
              icon={bull}
              to="/topics"
              selected={selected}
              setSelected={setSelected}
              
            />
           <Item
           title="Add Topic"
           icon={bull}
           to="/addnewtopic"
           selected={selected}
           setSelected={setSelected}
           /><br/>
           <Item
           title="MENTOR"
           icon={<PeopleOutlined/>}
           to="/addnewtopic"
           selected={selected}
           setSelected={setSelected}
           />
            <Item
              title="Mentors"
              icon={bull}
              to="/mentors"
              selected={selected}
              setSelected={setSelected}
            />  

            <Item
              title="Mentor Form"
              icon={bull}
              to="/addnewmentor"
              selected={selected}
              setSelected={setSelected}
            /><br/>
           
            <Button style={{textDecoration :"none", color  : "white" , paddingLeft: "10%", marginLeft: "2%" , fontSize:"18px"}} onClick={handleLogout} startIcon={<LogoutIcon/>}>Log out</Button>
            
          </Box>
        </Menu>
      </ProSidebar>
    </Box>
    
  );
};

export default Sidebar;
